.dashboard {
    padding: 20px;
  }
  
  .categories-grid {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 10px;
  }
  
  .subcategories-grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 10px;
  }
  
  .category, .subcategory {
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
    background-color: #f9f9f9;
    text-align: center;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .category img, .subcategory img {
    max-width: 100%;
    height: auto;
  }
  
  .category:hover, .subcategory:hover {
    background-color: #e0e0e0;
  }
  
  h2 {
    text-align: center;
  }
  
  button {
    margin: 10px;
  }
  
  input[type="text"], input[type="file"] {
    margin: 10px;
  }
  
  .image-picker img {
    max-width: 100px;
    margin: 10px;
    cursor: pointer;
  }
  
  .settings {
    padding: 20px;
  }
  
  .categories-settings, .subcategories-settings {
    margin: 20px 0;
  }
  
  .categories-settings > div, .subcategories-settings > div {
    margin: 10px 0;
  }
  